import React from 'react';
import {  useNavigate } from 'react-router-dom';
import Groups from './Groups';
import { useEffect } from 'react';
import { useDispatch,useSelector } from 'react-redux';
import { fetchGroups } from '../../reducers/groupSlice';
import { fetchAllUsers,fetchUser } from '../../reducers/groupSlice';
import { filterUsers } from '../../reducers/groupSlice';
const Home = () => {
  const users = useSelector((state)=>state.group.users);
  const user = useSelector((state)=>state.group.user)
  const dispatch = useDispatch()
  useEffect(()=>{
dispatch(fetchGroups())
dispatch(fetchAllUsers());
dispatch(fetchUser(4));
  },[])

    const navigate = useNavigate();
  return (
    <div>
      
<div class="bg-white dark:bg-gray-800 ">
    <div class="text-start w-full mx-auto py-10 px-4 sm:px-6 lg:py-16 lg:px-8 z-20">
        <h2 class="text-3xl font-extrabold text-black dark:text-white sm:text-4xl">
            <span class="block">
               Wanna split expense among group ?
            </span>
            <span class="block text-indigo-500">
              You&#x27;re at right place
            </span>
        </h2>
        <div class="lg:mt-0 lg:flex-shrink-0">
            <div class="mt-12 inline-flex rounded-md shadow">
                <button type="button" onClick={()=>{dispatch(filterUsers({users,user})); navigate('/addGroup'); }} class="py-4 px-6  bg-indigo-600 hover:bg-indigo-700 focus:ring-indigo-500 focus:ring-offset-indigo-200 text-white w-full transition ease-in duration-200 text-center text-base font-semibold shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2  rounded-lg ">
                  Create Group
                </button>
            </div>
        </div>
    </div>
</div>

      <Groups></Groups>
 
    </div>
  )
}

export default Home